'use client';

import { motion } from 'framer-motion';
import { Star, Shield, Zap, Award, Users } from 'lucide-react';
import { useAuth } from '@/context/AuthContext';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';

export default function Dashboard() {
    const { user, loading, signOut } = useAuth();
    const router = useRouter();
    const [isMounted, setIsMounted] = useState(false);

    useEffect(() => {
        setIsMounted(true);
        if (!loading && !user) {
            router.push('/');
        }
    }, [user, loading, router]);

    if (!isMounted || loading || !user) {
        return (
            <div className="min-h-screen bg-[#F0F2F5] flex items-center justify-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#7AC0E7]"></div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#F0F2F5] pb-24 relative">
            {/* Header Background */}
            <div className="h-48 bg-[#1F2E4A] rounded-b-[2.5rem] relative overflow-hidden">
                <div className="absolute top-0 left-0 w-full h-full opacity-10 bg-[url('https://www.transparenttextures.com/patterns/cubes.png')]"></div>
                <div className="absolute -bottom-10 -right-10 w-40 h-40 bg-[#7AC0E7] rounded-full blur-[50px] opacity-30"></div>

                {/* Logout Button (Temporary) */}
                <button
                    onClick={() => signOut()}
                    className="absolute top-4 right-4 bg-white/10 backdrop-blur-md px-3 py-1 rounded-full text-xs font-bold text-white border border-white/20"
                >
                    Logout
                </button>
            </div>

            <div className="px-6 -mt-20">
                {/* Profile Card */}
                <motion.div
                    initial={{ y: 20, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ duration: 0.5 }}
                    className="bg-white rounded-3xl p-6 shadow-xl border-4 border-white relative z-10 mt-16"
                >
                    <div className="flex flex-col items-center">
                        {/* Avatar */}
                        <div className="w-24 h-24 rounded-full bg-gradient-to-tr from-[#FFD700] to-[#FFB300] border-4 border-white shadow-md flex items-center justify-center -mt-20 mb-3 relative z-20 overflow-hidden">
                            {user?.photoURL ? (
                                <img src={user.photoURL} alt="User Avatar" className="w-full h-full object-cover" />
                            ) : (
                                <span className="text-4xl">ðŸ¦‰</span>
                            )}
                            <div className="absolute bottom-0 right-0 bg-[#4A90E2] text-white text-xs font-bold px-2 py-1 rounded-full border-2 border-white">
                                LV. 1
                            </div>
                        </div>

                        <h2 className="text-2xl font-bold text-[#1F2E4A] font-poppins">{user.displayName || 'Novice Adventurer'}</h2>
                        <div className="flex items-center gap-2 mt-1 mb-4">
                            <span className="px-3 py-1 bg-gray-100 text-gray-500 rounded-full text-xs font-bold border border-gray-200 flex items-center gap-1">
                                <Shield className="w-3 h-3 fill-current" />
                                ROOKIE RANK
                            </span>
                            <span className="px-3 py-1 bg-blue-50 text-blue-500 rounded-full text-xs font-bold border border-blue-100 flex items-center gap-1">
                                <Users className="w-3 h-3" />
                                No Party
                            </span>
                        </div>

                        {/* EXP Bar */}
                        <div className="w-full bg-gray-100 h-4 rounded-full overflow-hidden relative mb-2">
                            <motion.div
                                initial={{ width: 0 }}
                                animate={{ width: '10%' }}
                                transition={{ delay: 0.5, duration: 1 }}
                                className="h-full bg-gradient-to-r from-[#7AC0E7] to-[#4A90E2] rounded-full"
                            />
                        </div>
                        <div className="flex justify-between w-full text-xs font-bold text-gray-400 mb-6">
                            <span>EXP 0</span>
                            <span>Next Lvl: 100</span>
                        </div>

                        {/* Stats Grid */}
                        <div className="grid grid-cols-2 gap-3 w-full">
                            <div className="bg-blue-50 rounded-2xl p-3 flex flex-col items-center gap-1">
                                <Star className="text-yellow-400 fill-current w-6 h-6" />
                                <span className="text-2xl font-bold text-[#1F2E4A]">0</span>
                                <span className="text-[10px] font-bold text-gray-400 uppercase tracking-widest">Quests</span>
                            </div>
                            <div className="bg-pink-50 rounded-2xl p-3 flex flex-col items-center gap-1">
                                <Award className="text-pink-400 w-6 h-6" />
                                <span className="text-2xl font-bold text-[#1F2E4A]">0</span>
                                <span className="text-[10px] font-bold text-gray-400 uppercase tracking-widest">Badges</span>
                            </div>
                        </div>
                    </div>
                </motion.div>

                {/* Daily Quests Section */}
                <div className="mt-8">
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-bold text-[#1F2E4A]">Daily Quests</h3>
                        <span className="text-xs font-bold text-[#7AC0E7]">See All</span>
                    </div>

                    <div className="space-y-4">
                        {[1, 2, 3].map((i) => (
                            <motion.div
                                key={i}
                                whileTap={{ scale: 0.98 }}
                                className="bg-white p-4 rounded-2xl shadow-sm border border-gray-100 flex items-center gap-4"
                            >
                                <div className={`w-12 h-12 rounded-xl flex items-center justify-center ${i === 1 ? 'bg-green-100 text-green-500' : 'bg-orange-100 text-orange-500'}`}>
                                    {i === 1 ? <Zap className="w-6 h-6 fill-current" /> : <Star className="w-6 h-6" />}
                                </div>
                                <div className="flex-1">
                                    <h4 className="font-bold text-[#1F2E4A] text-sm">Win 1 Gym Battle</h4>
                                    <p className="text-xs text-gray-400 font-bold">+500 EXP</p>
                                </div>
                                <button className={`px-4 py-2 rounded-full text-xs font-bold ${i === 1 ? 'bg-gray-100 text-gray-400' : 'bg-[#7AC0E7] text-white'}`}>
                                    {i === 1 ? 'Done' : 'Go'}
                                </button>
                            </motion.div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}
